$(function(){
    $("#deleteFriendGroup").click(function(){
        var obj = document.getElementsByName("gid");//name="groupId"
        var s=[];//var s;snull
        for(var i=0;i<obj.length;i++){
            if(obj[i].checked) //
                s[i]=obj[i].value;   //values
        }
        if(s.length == 0){
            $.alert('','');
            return false;
        }

        $.confirm('\n','',
            function(){
                $("#delFGId").attr('value',s);
                $("#deleteGroupForm").submit();
            },function(){return false;});
    });

});

