$(document).on("click", "#equipSet", function() {
    $.actions({
        title: "",
        onClose: function() {
            console.log("close");
        },
        actions: [
            {
                text: "",
                className: "color-primary",
                onClick: function() {
                    //$.alert("");
                    var dm = window.location.host;
                    var tg = "/equipSet/setName";
                    var url = "http://"+dm+tg;
                    window.location.href=url;

                }
            },
        ]
    });
});
